@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base{
    /* write style here */
    html::-webkit-scrollbar{
        width: 0;
    }
}
@layer components{
    .child-hover>:hover{
        @apply text-white underline
    }
    .show-image{
        @apply absolute
    }
}
@layer utilities{
}



